body {
  background-color: black;
  background-image: url("./images/background-trans3.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: auto 100%;
  background-position-x: center;
  background-position-y: -1600%;
  padding: 0px 100px;
}
.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 1200ms ease-out, transform 600ms ease-out,
    visibility 1200ms ease-out;
  will-change: opacity, transform, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}
.wrapper .sidebar {
  background: black;
  position: fixed;
  z-index: 80;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  padding: 10px 0;
  transition: all 0.5s ease;
  display: table;
  font-family: Arial, Helvetica, sans-serif;
}
.shopButton {
  background: black;
  position: fixed;
  bottom: 5%;
  right: 0;
  border-radius: 10%;
  padding: 15px 15px 15px 15px;
}
.shopButton a:hover {
  opacity: 50%;
  cursor: pointer;
}
.wrapper .sidebar h3 {
  display: table-cell;
  color: white;
  margin: auto;
  vertical-align: middle;
  text-align: center;
}
.wrapper .sidebar li {
  display: table-cell;
  color: white;
  margin: auto;
  vertical-align: middle;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  width: 20%;
}
.wrapper .sidebar a:hover {
  opacity: 50%;
  cursor: pointer;
}
a {
  text-decoration: none;
}
.wrapper .sidebar img {
  display: table-cell;
  width: 80px;
  height: 80px;
  margin: auto;
